import React, { useState, useRef } from 'react';
import { Button, Typography, Grid } from '@mui/material';
import axios from 'axios';

function VoiceRecording() {
  const [isRecording, setIsRecording] = useState(false);
  const [audioURL, setAudioURL] = useState(null);
  const [transcript, setTranscript] = useState(null);
  const [summary, setSummary] = useState(null);
  const mediaRecorderRef = useRef(null); //  
  const audioChunksRef = useRef([]); //      

  const startRecording = async () => {
    setIsRecording(true); //  
    audioChunksRef.current = []; //    [] 

    try {
      const stream = await navigator.mediaDevices.getUserMedia({ audio: true });
      //   
      mediaRecorderRef.current = new MediaRecorder(stream);
      //     
      mediaRecorderRef.current.ondataavailable = (event) => {
        if (event.data.size > 0) {
          audioChunksRef.current.push(event.data);
        }
      };
      //     size 0   audioChunksRef 


      mediaRecorderRef.current.onstop = async () => {
        const audioBlob = new Blob(audioChunksRef.current, { type: 'audio/wav' });
        const audioUrl = URL.createObjectURL(audioBlob);
        setAudioURL(audioUrl);
        //     audioBlob wav  
        //  audioUrl     Url 
        // setAudioURL useState   

        const formData = new FormData();
        //    
        formData.append('file', audioBlob, 'recording.wav');
        // file :      
        // audioBlob :   
        // recording.wav :      

        try {
          const response = await axios.post('/api/audio/transcribe', formData, {
            headers: { 'Content-Type': 'multipart/form-data' },
          });
          //   post formData   headers     

          const transcribedText = response.data.transcript;
          setTranscript(transcribedText);
          //     (response.data.transcript) setTranscript useState   

          const gptResponse = await axios.post('/api/gpt/summarize', { text: transcribedText });
          console.log('GPT Response:', gptResponse.data); //    
          setSummary(gptResponse.data.summary);
          //   transcribedText /api/gpt/summarize post    gptResponse 
          // console.log   
          // setSummary useState   gptResponse   

            try {
                await axios.post('/api/transText/save', {
                    transText: transcribedText,
                    summeryText: gptResponse.data.summary,
                    //   transcribedText gptResponse    
                });
            } catch (error) {
                console.error('Error saving summary text:', error.response ? error.response.data : error.message);
                //        
            }

        } catch (error) {
          console.error('Error uploading audio:', error);
          //           
        }
      };

      mediaRecorderRef.current.start();
      //   start    
    } catch (error) {
      console.error('Error accessing microphone:', error);
      //         
    }
  };

  const stopRecording = () => {
    setIsRecording(false);
    if (mediaRecorderRef.current) {
      mediaRecorderRef.current.stop();
    }
  };
  //    setIsRecording useState  false 
  //   mediaRecorderRef.current    stop    

  return (
    <div style={{ padding: '20px' }}>
      <Typography variant="h4" gutterBottom>
           
      </Typography>
      {/* gutterBottom :       */}
      <Button
        variant="contained"
        color="primary"
        onClick={isRecording ? stopRecording : startRecording}
      >
        {isRecording ? ' ' : ' '}
      </Button>

      {audioURL && (
        <div>
          <Typography variant="h6"> :</Typography>
          <audio src={audioURL} controls />
        </div>
      )}
      {/* audioURL      
          controls      */}

        <Grid container spacing={2} style={{ marginTop: '20px' }}>
          <Grid item xs={12} md={6}> 
            {/*  
                xs : 12   1  
                md : 12   6   */}
            <Typography variant="h6">  :</Typography>
            <Typography>{transcript || '  .'}</Typography>
          </Grid>
          <Grid item xs={12} md={6}>
            <Typography variant="h6"> :</Typography>
            <Typography
              dangerouslySetInnerHTML={{
                __html: summary
                // dangerouslySetInnerHTML : HTML    
                // __html :  HTML ,  .
                  ? summary.replace(/\n/g, '<br />')
                  // summary  html  
                  //     <br /> 
                  : '  .'
              }}
            />
          </Grid>
        </Grid>
    </div>
  );
}

export default VoiceRecording;
