import React, { useEffect, useState } from 'react';
import { Button, Container, Typography } from '@mui/material';
import { useNavigate } from 'react-router-dom';
//    
import axios from 'axios';

function UserBoard() {
  const [boardLists, setBoardLists] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(false);

  const fetchBoardLists = async () => {
    try {
      const response = await axios.get('/api/board/list');
      setBoardLists(response.data);
      // /api/board/list get     response 
      //  response.data setBoardLists useState   
    } catch (error) {
      setError('    .');
      console.error('  : ', error);
    } finally {
      setLoading(false);
      // finally :      
      //    loading false 
    }
  };

  useEffect(() => {
    fetchBoardLists(); 
    //    fetchBoardLists  
  }, []);


  const navigate = useNavigate();
  //    

  const handleNavigateToSave = () => {
    navigate('/UserBoardSave');
  };

  return (
    <Container>
      <Typography variant="h4" gutterBottom>
        {/* gutterBottom :       */}
         
      </Typography>

      <Button
        variant="contained"
        color="primary"
        onClick={handleNavigateToSave}
        style={{ marginTop: '20px' }}
      >
           
      </Button>

      {loading ? (
        //      
        <Typography>Loading...</Typography>
      ) : error ? (
        <Typography color="error">{error}</Typography>
      ) : (
        <ul>
          {/*      */}
          {boardLists.map((board) => (
            // boardLists.map((board))   board   
            <li key={board.idx}>
              <Typography variant="h6">{board.boardName}</Typography>
              <Typography>{board.boardText}</Typography>
            </li>
          ))}
        </ul>
      )}
    </Container>
  );
}

export default UserBoard;
