import React, { useState } from 'react';
import { TextField, Button, Typography } from '@mui/material';
import axios from 'axios';

function UserBoardSave() {
  const [boardName, setBoardName] = useState('');
  const [boardText, setBoardText] = useState('');
  const [error, setError] = useState(null); //   

    const handleBoardSave = async () => {
      try {
        await axios.post('/api/board/save', {
          boardName: boardName,
          boardText: boardText,
        });
        alert('  ');
        window.location.href = '/UserBoard';
      } catch (error) {
        setError('  .');
      }
    };


  return (
    <div>
      <Typography variant="h5" gutterBottom>
          
      </Typography>

      <TextField
        label="Name"
        fullWidth
        margin="normal"
        value={boardName}
        onChange={(e) => setBoardName(e.target.value)} //  
      />
      <TextField
        label="Text"
        fullWidth
        margin="normal"
        value={boardText}
        onChange={(e) => setBoardText(e.target.value)} //  
      />

      {/*   */}
      {error && (
        <Typography color="error" style={{ marginTop: '10px' }}>
          {error}
        </Typography>
      )}

      <Button
        variant="contained"
        fullWidth
        style={{ marginTop: '20px', backgroundColor: '#000000' }}
        onClick={handleBoardSave}
      >
        BoardSave
      </Button>
    </div>
  );
}

export default UserBoardSave;
